html,
body,
#root {
  height: 100%;
}

* {
  font-family: "Roboto", sans-serif;
}

.content {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.header {
  font-size: 60px;
  font-weight: 100;
  letter-spacing: 20px;
}

@media only screen and (max-width: 600px) {
  .header {
    font-size: 30px;
    letter-spacing: 5px;
  }
}

.navbar {
  position: fixed;
  top: 5%;
  right: 0;
  width: 100%;
  margin-right: 15%;
  height: 70%;
}

.navbar-line {
  height: 100%;
  width: 1px;
  opacity: 0.4;
  margin-left: 100%;
  border-right: 4px solid black;
}
.navbar-item-box > div > h3 {
  position: fixed;
  top: 10%;
  margin-left: 101%;
  width: 100%;
}

.navbar-item-box {
  top: 0;
  width: 100%;
  height: 100%;
  position: fixed;
}
.navbar-item {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-weight: 100;
  font-size: 30px;
  color: black;
}
.navbar-item:hover {
  color: white;
  cursor: pointer;
}
.item-1 {
  top: 20%;
  margin-top: 0%;
}
.item-2 {
  margin-top: 10%;
}
.item-3 {
  margin-top: 20%;
}
.item-4 {
  margin-top: 30%;
}
.focused {
  color: white;
}

/* MOBILE */
.navbar-mobile {
  width: 100%;
  position: fixed;
  top: 0;
  height: 30px;
}
.navbar-item-mobile {
  font-weight: 100;
}
.navbar-item-box-mobile {
  display: flex;
  justify-content: space-around;
}

